import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  Plus,
  Search,
  ChevronDown,
  Filter,
  Calendar,
  Tag,
  Eye,
  Edit,
  Trash2,
  BarChart,
  Clock,
  ArrowUpRight,
  X,
  AlertCircle,
} from "lucide-react";
import { motion } from "framer-motion";
import toast from "react-hot-toast";
import { Helmet } from "react-helmet";

// Empty State Component
const BlogTableEmptyState = ({ isFiltered }) => (
  <div className="py-12 px-4 text-center bg-white dark:bg-zinc-800 rounded-lg">
    <div className="flex flex-col items-center justify-center space-y-3">
      <div className="bg-gray-100 dark:bg-zinc-700 p-3 rounded-full">
        {isFiltered ? (
          <Filter className="w-8 h-8 text-gray-400 dark:text-gray-300" />
        ) : (
          <AlertCircle className="w-8 h-8 text-gray-400 dark:text-gray-300" />
        )}
      </div>
      <h3 className="text-lg font-medium text-gray-900 dark:text-gray-100">
        {isFiltered
          ? "هیچ مقاله‌ای با فیلترهای انتخاب شده یافت نشد"
          : "هنوز هیچ مقاله‌ای ایجاد نشده است"}
      </h3>
      <p className="text-gray-500 dark:text-gray-400 max-w-sm mx-auto">
        {isFiltered
          ? "لطفاً فیلترهای خود را تغییر دهید یا جستجوی دیگری انجام دهید"
          : "با کلیک بر روی دکمه «افزودن مقاله جدید» اولین مقاله خود را ایجاد کنید"}
      </p>
      {isFiltered && (
        <button
          onClick={() => window.location.reload()}
          className="mt-2 px-4 py-2 text-sm bg-blue-50 text-blue-600 dark:bg-blue-900/20 dark:text-blue-400 rounded-lg hover:bg-blue-100 dark:hover:bg-blue-900/30 transition-colors font-medium"
        >
          نمایش تمام مقالات
        </button>
      )}
    </div>
  </div>
);

// Blog Card Component for Mobile View
const BlogCard = ({ blog, handleDelete, getSEOScoreColor }) => {
  const [isExpanded, setIsExpanded] = useState(false);

  const formatDate = (dateString) => {
    try {
      const date = new Date(dateString);
      return new Intl.DateTimeFormat("fa-IR", {
        year: "numeric",
        month: "long",
        day: "numeric",
      }).format(date);
    } catch (e) {
      return dateString;
    }
  };

  const parseAndFormatTags = (tags) => {
    if (!tags || !tags.length) return [];

    return tags
      .map((tag) => {
        try {
          if (typeof tag === "string") {
            return JSON.parse(tag.replace(/'/g, '"')).name;
          } else if (tag && tag.name) {
            return JSON.parse(tag.name.replace(/'/g, '"')).name;
          }
          return "";
        } catch (e) {
          return "";
        }
      })
      .filter((tag) => tag);
  };

  const statusStyles = {
    published:
      "bg-green-100 text-green-700 dark:bg-green-900/20 dark:text-green-400",
    draft:
      "bg-yellow-100 text-yellow-700 dark:bg-yellow-900/20 dark:text-yellow-400",
  };

  return (
    <div className="bg-white dark:bg-zinc-800 rounded-lg shadow-md overflow-hidden mb-4">
      <div
        className="p-4 cursor-pointer"
        onClick={() => setIsExpanded(!isExpanded)}
      >
        <div className="flex justify-between items-center">
          <div className="flex-1">
            <div className="flex items-center gap-2 mb-1">
              <span
                className={`px-2 py-0.5 text-xs font-medium rounded-full ${
                  statusStyles[blog.status]
                }`}
              >
                {blog.status === "published" ? "منتشر شده" : "پیش‌نویس"}
              </span>
              {blog.seo_score && (
                <span
                  className={`flex items-center text-xs ${getSEOScoreColor(
                    blog.seo_score
                  )}`}
                >
                  <BarChart size={12} className="mr-1" />
                  {blog.seo_score}
                </span>
              )}
            </div>
            <h3 className="font-medium text-gray-900 dark:text-gray-100 line-clamp-1">
              {blog.title}
            </h3>
            <p className="text-xs text-gray-500 dark:text-gray-400 mt-1">
              {formatDate(blog.created_at || blog.date)}
            </p>
          </div>
          <ChevronDown
            className={`text-gray-400 transition-transform ${
              isExpanded ? "rotate-180" : ""
            }`}
            size={20}
          />
        </div>
      </div>

      {isExpanded && (
        <div className="px-4 pb-4 border-t border-gray-100 dark:border-zinc-700 pt-2">
          <div className="space-y-3">
            {blog.excerpt && (
              <p className="text-sm text-gray-600 dark:text-gray-300">
                {blog.excerpt}
              </p>
            )}

            {parseAndFormatTags(blog.tags).length > 0 && (
              <div className="flex flex-wrap gap-1">
                {parseAndFormatTags(blog.tags).map((tag, index) => (
                  <span
                    key={index}
                    className="inline-flex items-center px-2 py-1 rounded-full text-xs bg-gray-100 text-gray-700 dark:bg-zinc-700 dark:text-gray-300"
                  >
                    <Tag size={12} className="mr-1" />
                    {tag}
                  </span>
                ))}
              </div>
            )}

            <div className="flex flex-wrap items-center justify-between gap-2 pt-2">
              <div className="flex space-x-2 space-x-reverse">
                <Link to={`/dashboard/blogs/edit/${blog.id}`}>
                  <button className="flex items-center justify-center p-2 rounded-lg bg-blue-50 text-blue-600 hover:bg-blue-100 dark:bg-blue-900/20 dark:text-blue-400 dark:hover:bg-blue-900/30 transition-colors">
                    <Edit size={16} />
                  </button>
                </Link>
                <button
                  onClick={(e) => {
                    e.stopPropagation();
                    handleDelete(blog.id);
                  }}
                  className="flex items-center justify-center p-2 rounded-lg bg-red-50 text-red-600 hover:bg-red-100 dark:bg-red-900/20 dark:text-red-400 dark:hover:bg-red-900/30 transition-colors"
                >
                  <Trash2 size={16} />
                </button>
              </div>

              {blog.status === "published" && blog.url && (
                <a
                  href={blog.url}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="flex items-center text-xs font-medium text-blue-600 dark:text-blue-400 hover:underline"
                  onClick={(e) => e.stopPropagation()}
                >
                  مشاهده در سایت
                  <ArrowUpRight size={14} className="mr-1" />
                </a>
              )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

const BlogManagement = () => {
  const [blogs, setBlogs] = useState([]);
  const [searchTerm, setSearchTerm] = useState("");
  const [filterStatus, setFilterStatus] = useState("all");
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  const API_URL =
    window.location.hostname === "localhost"
      ? "http://127.0.0.1:8000/api/blog/"
      : "https://allmaxsport.com/api/blog/";

  useEffect(() => {
    async function loadBlogs() {
      try {
        setIsLoading(true);
        const response = await fetch(API_URL);
        if (!response.ok) {
          throw new Error("Failed to fetch blogs");
        }
        const data = await response.json();
        setBlogs(data);
        setError(null);
      } catch (err) {
        setError("خطا در بارگذاری مقالات. لطفاً صفحه را مجدداً بارگذاری کنید.");
      } finally {
        setIsLoading(false);
      }
    }
    loadBlogs();
  }, []);

  const handleDelete = (id) => {
    if (window.confirm("آیا مطمئن هستید که می‌خواهید این مقاله را حذف کنید؟")) {
      setIsLoading(true);
      fetch(`${API_URL}?id=${id}`, {
        method: "DELETE",
      })
        .then((res) => {
          if (!res.ok) throw new Error("Failed to delete blog");
          toast.success("مقاله با موفقیت حذف شد!");
          setBlogs(blogs.filter((blog) => blog.id !== id));
        })
        .catch((err) => {
          toast.error("خطا در حذف مقاله. لطفاً مجدداً تلاش کنید.");
        })
        .finally(() => setIsLoading(false));
    }
  };

  const filteredBlogs = blogs.filter((blog) => {
    const matchesSearch =
      blog?.title?.toLowerCase().includes(searchTerm.toLowerCase()) ||
      blog?.tags?.some((tag) => {
        try {
          const realTagName = JSON.parse(tag.name.replace(/'/g, '"')).name;
          return realTagName.toLowerCase().includes(searchTerm.toLowerCase());
        } catch (e) {
          return false;
        }
      });

    const matchesStatus =
      filterStatus === "all" || blog.status === filterStatus;

    return matchesSearch && matchesStatus;
  });

  const getSEOScoreColor = (score) => {
    if (score >= 90) return "text-green-500 dark:text-green-400";
    if (score >= 70) return "text-yellow-500 dark:text-yellow-400";
    return "text-red-500 dark:text-red-400";
  };

  const clearSearch = () => {
    setSearchTerm("");
  };

  const clearFilters = () => {
    setSearchTerm("");
    setFilterStatus("all");
  };

  const isFiltered = searchTerm !== "" || filterStatus !== "all";

  return (
    <>
      <Helmet>
        <title>المکس | مقالات</title>
      </Helmet>
      <div className="min-h-screen bg-gray-100 dark:bg-zinc-900" dir="rtl">
        <div className="p-4 md:p-6 max-w-7xl mx-auto">
          <div className="flex flex-col md:flex-row md:justify-between md:items-center gap-4 mb-6">
            <h1 className="text-xl md:text-2xl font-bold text-gray-900 dark:text-gray-100">
              مدیریت بلاگ
            </h1>
            <Link to="/dashboard/blogs/add">
              <motion.button
                whileHover={{ scale: 1.03 }}
                whileTap={{ scale: 0.97 }}
                className="flex items-center justify-center px-4 py-2 bg-green-500 text-white rounded-lg shadow hover:bg-green-600 transition-colors w-full md:w-auto"
              >
                <Plus className="w-5 h-5 ml-2" />
                افزودن مقاله جدید
              </motion.button>
            </Link>
          </div>

          <div className="mb-6 bg-white dark:bg-zinc-800 p-4 rounded-lg shadow-sm">
            <div className="flex flex-col md:flex-row gap-4">
              <div className="relative flex-1">
                <Search className="w-5 h-5 absolute right-3 top-2.5 text-gray-400" />
                <input
                  type="text"
                  placeholder="جستجو در عنوان و برچسب‌ها..."
                  className="w-full pr-10 p-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 dark:bg-zinc-700 dark:border-zinc-600 dark:text-white"
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                />
                {searchTerm && (
                  <button
                    onClick={clearSearch}
                    className="absolute left-3 top-2.5 text-gray-400 hover:text-gray-600 dark:hover:text-gray-200"
                  >
                    <X size={18} />
                  </button>
                )}
              </div>
              <div className="flex-shrink-0">
                <div className="relative">
                  <select
                    value={filterStatus}
                    onChange={(e) => setFilterStatus(e.target.value)}
                    className="appearance-none w-full px-4 py-2 pr-10 rounded-lg bg-white dark:bg-zinc-700 border border-gray-300 dark:border-zinc-600 text-gray-700 dark:text-white text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
                  >
                    <option value="all">همه مقالات</option>
                    <option value="published">منتشر شده</option>
                    <option value="draft">پیش‌نویس</option>
                  </select>
                  <div className="pointer-events-none absolute inset-y-0 left-0 flex items-center pl-3">
                    <ChevronDown className="h-4 w-4 text-gray-400" />
                  </div>
                </div>
              </div>
            </div>

            {isFiltered && (
              <div className="flex items-center mt-4 text-sm">
                <span className="text-gray-500 dark:text-gray-400 ml-2">
                  فیلترهای فعال:
                </span>
                {searchTerm && (
                  <span className="bg-blue-50 dark:bg-blue-900/20 text-blue-600 dark:text-blue-400 px-2 py-1 rounded-md text-xs flex items-center mr-2">
                    جستجو: {searchTerm}
                    <button onClick={clearSearch} className="ml-1">
                      <X size={12} />
                    </button>
                  </span>
                )}
                {filterStatus !== "all" && (
                  <span className="bg-blue-50 dark:bg-blue-900/20 text-blue-600 dark:text-blue-400 px-2 py-1 rounded-md text-xs flex items-center">
                    وضعیت:{" "}
                    {filterStatus === "published" ? "منتشر شده" : "پیش‌نویس"}
                    <button
                      onClick={() => setFilterStatus("all")}
                      className="ml-1"
                    >
                      <X size={12} />
                    </button>
                  </span>
                )}
                <button
                  onClick={clearFilters}
                  className="text-gray-500 dark:text-gray-400 hover:text-gray-700 dark:hover:text-gray-200 text-xs mr-auto"
                >
                  پاک کردن فیلترها
                </button>
              </div>
            )}
          </div>

          {isLoading ? (
            <div className="bg-white dark:bg-zinc-800 rounded-lg shadow-sm p-8 text-center">
              <div className="inline-block animate-spin rounded-full h-8 w-8 border-4 border-gray-300 dark:border-zinc-600 border-t-blue-500" />
              <p className="mt-4 text-gray-500 dark:text-gray-400">
                در حال بارگذاری مقالات...
              </p>
            </div>
          ) : error ? (
            <div className="bg-white dark:bg-zinc-800 rounded-lg shadow-sm p-6 text-center">
              <AlertCircle className="h-10 w-10 text-red-500 mx-auto mb-4" />
              <p className="text-gray-700 dark:text-gray-300">{error}</p>
              <button
                onClick={() => window.location.reload()}
                className="mt-4 px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors"
              >
                تلاش مجدد
              </button>
            </div>
          ) : (
            <>
              {/* Mobile view: Cards */}
              <div className="md:hidden">
                {filteredBlogs?.length > 0 ? (
                  filteredBlogs.map((blog) => (
                    <BlogCard
                      key={blog.id}
                      blog={blog}
                      handleDelete={handleDelete}
                      getSEOScoreColor={getSEOScoreColor}
                    />
                  ))
                ) : (
                  <BlogTableEmptyState isFiltered={isFiltered} />
                )}
              </div>

              {/* Desktop view: Table */}
              <div className="hidden md:block bg-white dark:bg-zinc-800 rounded-lg shadow-sm overflow-hidden">
                {filteredBlogs?.length > 0 ? (
                  <div className="overflow-x-auto">
                    <table className="min-w-full divide-y divide-gray-200 dark:divide-zinc-700">
                      <thead className="bg-gray-50 dark:bg-zinc-700">
                        <tr>
                          <th
                            scope="col"
                            className="px-6 py-3 text-right text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider"
                          >
                            عنوان
                          </th>
                          <th
                            scope="col"
                            className="px-6 py-3 text-right text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider"
                          >
                            وضعیت
                          </th>
                          <th
                            scope="col"
                            className="px-6 py-3 text-right text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider"
                          >
                            تاریخ
                          </th>
                          <th
                            scope="col"
                            className="px-6 py-3 text-right text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider"
                          >
                            امتیاز SEO
                          </th>
                          <th
                            scope="col"
                            className="px-6 py-3 text-right text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider"
                          >
                            برچسب‌ها
                          </th>
                          <th
                            scope="col"
                            className="px-6 py-3 text-right text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider"
                          >
                            عملیات
                          </th>
                        </tr>
                      </thead>
                      <tbody className="bg-white dark:bg-zinc-800 divide-y divide-gray-200 dark:divide-zinc-700">
                        {filteredBlogs.map((blog) => {
                          const formatDate = (dateString) => {
                            try {
                              const date = new Date(dateString);
                              return new Intl.DateTimeFormat("fa-IR").format(
                                date
                              );
                            } catch (e) {
                              return dateString;
                            }
                          };

                          const parseAndFormatTags = (tags) => {
                            if (!tags || !tags.length) return [];

                            return tags
                              .map((tag) => {
                                try {
                                  if (typeof tag === "string") {
                                    return JSON.parse(tag.replace(/'/g, '"'))
                                      .name;
                                  } else if (tag && tag.name) {
                                    return JSON.parse(
                                      tag.name.replace(/'/g, '"')
                                    ).name;
                                  }
                                  return "";
                                } catch (e) {
                                  return "";
                                }
                              })
                              .filter((tag) => tag);
                          };

                          return (
                            <tr
                              key={blog.id}
                              className="hover:bg-gray-50 dark:hover:bg-zinc-700 transition-colors"
                            >
                              <td className="px-6 py-4 whitespace-nowrap">
                                <div className="flex items-center">
                                  <div className="text-sm font-medium text-gray-900 dark:text-white">
                                    {blog.title}
                                  </div>
                                </div>
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap">
                                <span
                                  className={`px-2 py-1 inline-flex text-xs leading-5 font-medium rounded-full ${
                                    blog.status === "published"
                                      ? "bg-green-100 text-green-800 dark:bg-green-900/20 dark:text-green-400"
                                      : "bg-yellow-100 text-yellow-800 dark:bg-yellow-900/20 dark:text-yellow-400"
                                  }`}
                                >
                                  {blog.status === "published"
                                    ? "منتشر شده"
                                    : "پیش‌نویس"}
                                </span>
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap">
                                <div className="text-sm text-gray-500 dark:text-gray-400 flex items-center">
                                  <Calendar className="w-4 h-4 ml-1" />
                                  {formatDate(blog.created_at || blog.date)}
                                </div>
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap">
                                {blog.seo_score ? (
                                  <div
                                    className={`flex items-center ${getSEOScoreColor(
                                      blog.seo_score
                                    )}`}
                                  >
                                    <BarChart className="w-4 h-4 ml-1" />
                                    {blog.seo_score}
                                  </div>
                                ) : (
                                  <span className="text-gray-400 dark:text-gray-500">
                                    -
                                  </span>
                                )}
                              </td>
                              <td className="px-6 py-4">
                                <div className="flex flex-wrap gap-1 max-w-xs">
                                  {parseAndFormatTags(blog.tags).map(
                                    (tag, index) => (
                                      <span
                                        key={index}
                                        className="inline-flex items-center px-2 py-0.5 rounded-full text-xs bg-gray-100 text-gray-800 dark:bg-zinc-700 dark:text-gray-300"
                                      >
                                        {tag}
                                      </span>
                                    )
                                  )}
                                </div>
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                                <div className="flex items-center space-x-3 space-x-reverse">
                                  {blog.status === "published" && blog.url && (
                                    <a
                                      href={blog.url}
                                      target="_blank"
                                      rel="noopener noreferrer"
                                      className="text-blue-600 dark:text-blue-400 hover:text-blue-900 dark:hover:text-blue-300"
                                      title="مشاهده در سایت"
                                    >
                                      <Eye size={18} />
                                    </a>
                                  )}
                                  <Link
                                    to={`/dashboard/blogs/edit/${blog.id}`}
                                    className="text-indigo-600 dark:text-indigo-400 hover:text-indigo-900 dark:hover:text-indigo-300"
                                    title="ویرایش"
                                  >
                                    <Edit size={18} />
                                  </Link>
                                  <button
                                    onClick={() => handleDelete(blog.id)}
                                    className="text-red-600 dark:text-red-400 hover:text-red-900 dark:hover:text-red-300"
                                    title="حذف"
                                  >
                                    <Trash2 size={18} />
                                  </button>
                                </div>
                              </td>
                            </tr>
                          );
                        })}
                      </tbody>
                    </table>
                  </div>
                ) : (
                  <BlogTableEmptyState isFiltered={isFiltered} />
                )}
              </div>
            </>
          )}
        </div>
      </div>
    </>
  );
};

export default BlogManagement;
